import React, { useState } from 'react';
import { Vehicle } from '../../types/vehicles';
import AddVehiclePage from '../../view/components/add-vehicle-page/add-vehicle-page';

const AddVehicleContainer = () => {
  const [value, setValue] = useState<Vehicle>({
    id: 313135134135,
    manufacturer: '',
    model: '',
    year: '',
    color: '',
    car_license_plate: '',
    vin: '',
    license_card: '',
    osago: '',
    end_osago: '',
    diagnostic_card: '',
    end_diagnostic_card: '',
    permission_number: '',
    organization_id: 343438483,
    organization: { id: 343438483, name: 'Any company' },
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    setValue({ ...value, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    alert(JSON.stringify(value));
  };

  return <AddVehiclePage handleSubmit={handleSubmit} handleChange={handleChange} value={value} />;
};

export default AddVehicleContainer;
